import {  useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { useNavigate } from "react-router-dom"
import {  queryNewPost } from "../../redux/actionCreators/postAC"

import './style.css'
const Form = () => {
  const dispatch = useDispatch()
const navigate = useNavigate()
   const [title, setTitle] = useState('')
   const [text, setText] = useState('')
   const [image, setImage] = useState('')
   const [tags, setTags] = useState('')
/*   */
const changeTitle = (e) => {
    setTitle(e.target.value)}
const changeText = (e) => {
    setText(e.target.value)}
const changeImg = (e) => {
    setImage(e.target.value) }
const changeTag = (e) => {
    setTags(e.target.value) }
/*     */
const person = useSelector((store) => store.person);
const submitHandler = () => {
  const preparedPostQuery = {
    title,
    text,
    image,
    tags: tags.split(',').map((el) => el.trim()) 
  }
  const body = JSON.stringify(preparedPostQuery);
  dispatch(queryNewPost(body, person.token));
    setTitle('')
    setText('')
    setImage('')
    setTags('')
    navigate(`/`)
      }
     
       
   return (
     <>

     <div>
       <img  className="Post_img" src="logo.png" alt="Logo-big"></img>
     </div>
<div>
  <form onSubmit={submitHandler} className="d-flex flex-column align-items-center">
    <div className="mb-3">
      <label for="postcard__title" 
      className="form-label title">  !</label>
      <input 
      onChange={changeTitle}
      type="text" 
      className="form-control " 
      id="postcard__title" 
      aria-describedby="emailHelp" 
      value={title}
      placeholder="..."
      required
      />
    </div>
    <div className="mb-3">
    <div className="form-group">
    <label for="postcard__desc"></label>
    <textarea 
    onChange={changeText}
    className="form-control pole" 
    id="postcard__desc" 
    rows="3"
    value={text}
    placeholder=" ..."
  
    >
      
    </textarea>
  </div>
    </div>
    <div 
    className="mb-3">
      <label for="postcard__img"
       className="form-label img"></label>
      <input 
      onChange={changeImg}
      type="text" 
      className="form-control" 
      id="postcard__img" 
      value={image}
      placeholder="   "
      required
      />
    </div>
    <div 
    className="mb-3">
      <label for="postcard__tags"
       className="form-label tag">:</label>
      <input 
      onChange={changeTag}
      type="text" 
      className="form-control" 
      id="postcard__tags" 
      value={tags}
      placeholder="      "
      />
     
    </div>
        <button 
    type="submit" 
    className="btn btn-primary">
        
        </button>
  </form>
</div>
     </>
    
   ) 
}
export default Form